// çµ±åˆãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹ãƒ†ã‚¹ãƒˆï¼ˆBLUEæ®µéšãƒªãƒ•ã‚¡ã‚¯ã‚¿ãƒªãƒ³ã‚°æ¤œè¨¼ï¼‰
describe('TemplateParserçµ±åˆãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹ï¼ˆCLAUDE.mdæœ€é©åŒ–ï¼‰', () => {

  it('should demonstrate overall parsing efficiency', async () => {
    const parser = new TemplateParser();
    const examples = [
      'serverless-api-sam.yaml',
      'container-microservices-ecs.yaml', 
      'web-application-stack.yaml'
    ];

    const results = [];
    let totalDuration = 0;
    
    for (const example of examples) {
      const templatePath = path.join(__dirname, '../../../examples', example);
      const startTime = performance.now();
      
      const template = await parser.parse(templatePath);
      const duration = performance.now() - startTime;
      
      totalDuration += duration;
      results.push({
        file: example,
        duration: Math.round(duration),
        resourceCount: Object.keys(template.Resources).length
      });
    }

    expect(totalDuration).toBeLessThan(3000); // å…¨ã¦åˆã‚ã›ã¦3ç§’ä»¥å†…
    
    console.log('ğŸ“ˆ è§£æãƒ‘ãƒ•ã‚©ãƒ¼ãƒãƒ³ã‚¹çµæœ:');
    results.forEach(result => {
      console.log(`   ${result.file}: ${result.duration}ms (${result.resourceCount}ãƒªã‚½ãƒ¼ã‚¹)`);
    });
    console.log(`   åˆè¨ˆ: ${Math.round(totalDuration)}ms`);
  });
});